
import CameraAltIcon from '@material-ui/icons/CameraAlt';
import CodeIcon from '@material-ui/icons/Code';
import MovieIcon from '@material-ui/icons/Movie';
import { useEffect } from 'react';
// import { useState } from 'react';
import recruit from '../recruit.png';
import social from '../social.png';
import './home.component.scss'
import me from '../assets/me.jpg';


export default function Home() {

    // const [scrollTop, setScrollTop] = useState(0);

    // const [positiontext, setpositiontext] = useState('TOP');
    // // const [action, setAction] = useState(false);
    // let [position, setPosition] = useState(0)
    // const [ticking, setTicking] = useState(false)

    useEffect(() => {
        var wrapper, content, test;
        var more = '<div style="height:1000px; background:transparent;z-index:-1"></div>';
        let revealers = document.querySelectorAll('.revealer');
        // this is the scroll event handler
        function scroller() {
            // print relevant scroll info
            test.innerHTML = wrapper.scrollTop;
            // + " + " + wrapper.offsetHeight + " + 100 > " + content.offsetHeight;

            // add more contents if user scrolled down enough
            if (wrapper.scrollTop + wrapper.offsetHeight + 100 > content.offsetHeight) {
                content.innerHTML += more; // NK: Here you can make an Ajax call and fetch content to append to content.innerHTML
            }

            if (wrapper.scrollTop > 10 && wrapper.scrollTop < 1000) {
                revealers[0].classList.add('active');
            } else {
                revealers[0].classList.remove('active');
            }
            if (wrapper.scrollTop > 1000 && wrapper.scrollTop < 2000) {
                revealers[1].classList.add('active');
            } else {
               
                revealers[1].classList.remove('active');
            }
        }

        wrapper = document.getElementById("wrapper");
        content = document.getElementById("content");
        test = document.getElementById("test");

        content.innerHTML = more;

        // hook the scroll handler to scroll event
        if (wrapper.addEventListener) // NK: Works on all new browsers
            wrapper.addEventListener("scroll", scroller, false);

        else if (wrapper.attachEvent) // NK: Works on old IE
            wrapper.attachEvent("onscroll", scroller);


    });
    return (

        <div>


            <div id="test"></div>

            <div id="wrapper" class="inf">
                <div id="content">
                </div> </div>

            <div className="App" id="App">


                <div class="ins"> Scroll to view more </div>

                <div className="App-header">


                    <p className="profile revealer fromright">

                        <p className="icon-group">
                            <CameraAltIcon className="icon icon-cam"></CameraAltIcon>
                            <CodeIcon className="icon icon-code"></CodeIcon>
                            <MovieIcon className="icon icon-movie"></MovieIcon>
                        </p>

                        <img className="App-logo revealme fromright" src={me} alt="Logo" />
                        <h1 className="revealme fromleft"> Vaisakh M A </h1>
                        <p className="revealme fromright"> <span >Hi, I'm a   </span>

                            <span class="toBeReplaced"> </span>
                        </p>
                    </p>
                    <div class='entry-buttons revealer fromleft'>

                        <div class="options revealme fromleft"> <p> <img alt="recruit" src={recruit}></img> </p>  <p> You're a Recruiter</p></div>
                        <div class="options revealme fromright"> <p> <img alt="social" src={social}></img>   </p>  <p> You're a Follower</p></div>

                    </div>
                </div>



            </div>


        </div>





    )
}
